import React, { useState } from "react";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import { Button } from "@material-ui/core";
import { useHistory } from "react-router-dom";

function BottomBar() {
  const [appBarColor, setAppBarColor] = useState("#dbe2ef");
  let history = useHistory();

  function handleNavClick(color, url) {
    if (color !== appBarColor) {
      history.push(url);
      setAppBarColor(color);
      window.scrollTo(0, 0);
    }
  }

  return (
    <div className="container fixed-bottom">
      <div className="row justify-content-center">
        <AppBar
          position="relative"
          className="col-xl-4 col-lg-5 col-md-6 col-11 mx-auto justify-content-center shadow-lg"
          style={{
            backgroundColor: appBarColor,
            bottom: "20px",
            alignItems: "center",
            borderRadius: "15px",
          }}
        >
          <Toolbar>
            <Button
              onClick={() =>
                handleNavClick("#dbe2ef", process.env.PUBLIC_URL + "/")
              }
              className="mr-3"
              variant="contained"
              style={{ backgroundColor: "#dbe2ef", color: "#FFFF" }}
            >
              Home
            </Button>

            <Button
              onClick={() =>
                handleNavClick("#fab2ac", process.env.PUBLIC_URL + "Portfolio")
              }
              className="mr-3"
              variant="contained"
              style={{ backgroundColor: "#fab2ac", color: "#FFFF" }}
            >
              Portfolio
            </Button>

            <Button
              onClick={() =>
                handleNavClick("#eda1c1", process.env.PUBLIC_URL + "Resume")
              }
              variant="contained"
              style={{ backgroundColor: "#eda1c1", color: "#FFFF" }}
            >
              Resume
            </Button>
          </Toolbar>
        </AppBar>
      </div>
    </div>
  );
}

export default BottomBar;
